.weather-container {
    width: 40%;
    min-height: 400px;
    border-radius: 7px;
    border: none;
    outline: none;
    box-shadow: 0px 1px 13px 1px rgba(0, 0, 0, 0.98);
    justify-content: center;
    align-items: center;
    margin: 7% auto;
    transition: all ease 0.7s;
    animation-name: weather;
    animation-duration: 2s;
    animation-timing-function: linear;
}

@keyframes weather {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

.search-bar {
    width: 80%;
    margin: auto;
    padding: 10px;
    min-height: 50px;
    border: none;
    outline: none;
    border-radius: 10px;
}

#search {
    border-bottom: 1px solid #fff;
    border-top: none;
    border-left: none;
    border-right: none;
    width: 100%;
    height: 30px;
    margin-top: 10px;
}

#search:focus {
    outline: none;
}

#search::placeholder {
    font-size: 20px;
}

.city-name {
    text-align: center;
    font-size: 40px;
    font-weight: 600;
    overflow: hidden;
}

.temperature {
    display: flex;
    justify-content: space-between;
    padding: 20px;
}

.discreption {
    display: flex;
    justify-content: space-between;
    padding: 20px;
}

.extra-data {
    display: flex;
    justify-content: center;
    padding: 10px;
}

@media screen and (max-width: 750px) {
    .weather-container {
        width: 80%;
        margin: 25% auto;
    }
    .city-name {
        font-size: 25px;
    }
}